filterSelection("all") // Выполните функцию и покажите все столбцы
function filterSelection(c) {
  var card, i;
  card = document.getElementsByClassName("portfolio__cards");
  if (c == "all") c = "";
  // Добавить класс "show" (display:block) к фильтрованным элементам, и
  // извлеките класс "show" из элементов, которые не выбраны
  for (i = 0; i < card.length; i++) {
    w3RemoveClass(card[i], "show");
    if (card[i].className.indexOf(c) > -1) w3AddClass(card[i], "show");
  }
}


// Показать отфильтрованные элементы
function w3AddClass(element, name) {
  var i, arr1, arr2;
  arr1 = element.className.split(" ");
  arr2 = name.split(" ");
  for (i = 0; i < arr2.length; i++) {
    if (arr1.indexOf(arr2[i]) == -1) {
      element.className += " " + arr2[i];
    }
  }
}

// Скрыть элементы, которые не выбраны
function w3RemoveClass(element, name) {
  var i, arr1, arr2;
  arr1 = element.className.split(" ");
  arr2 = name.split(" ");
  for (i = 0; i < arr2.length; i++) {
    while (arr1.indexOf(arr2[i]) > -1) {
      arr1.splice(arr1.indexOf(arr2[i]), 1);
    }
  }
  element.className = arr1.join(" ");
}

